import React from "react";
import {
    Chart,
    Interval,
    Tooltip,
    Legend,
    Axis,
    Coordinate
}  from 'bizcharts';
import { DataView } from '@antv/data-set';


export default class PieChart extends React.Component {
    render() {
        const data = this.props.data;
        const dv = new DataView();
        dv.source(data).transform({
            type: 'percent',
            field: 'value',
            dimension: 'type',
            as: 'percent',
        });

        const dv1 = new DataView();
        dv1.source(data).transform({
            type: 'percent',
            field: 'value',
            dimension: 'name',
            as: 'percent',
        });

        return (
            <Chart
                height={170}
                data={dv.rows}
                autoFit
                scale={{
                    percent: {
                        formatter: (val) => {
                            val = (val * 100).toFixed(2) + '%';
                            return val;
                        },
                    }
                }}
            >
                <Coordinate type="theta" radius={0.5} />
                <Axis visible={false} />
                <Legend visible={false} />
                <Tooltip showTitle={false} />
                <Interval 
                    position="percent"
                    adjust="stack"
                    color={['name', ['#296F3E', '#B7C04F', '#111111', '#62C2CE']]}
                    element-highlight
                    style={{
                        lineWidth: 1,
                        stroke: '#fff',
                    }}
                    
                    
                />
                
            </Chart>
        );
        
    }
}