import React from "react";
import { Chart, Line } from 'bizcharts';




export default class LineChart extends React.Component {

    render() {

        
        return (
            <Chart scale={{ value: { min: 0 } }} padding={[10, 20, 50, 40]} autoFit height={302} data={this.props.data} >
                <Line
                    shape="smooth"
                    position="month*animalCount"
                    color={this.props.color}
                />
            </Chart>
        );

    }
}