import React, { Component } from 'react';
import Header from '../components/Header/Header';
import DashboardContainer from '../containers/DashboardContainer'
import '../assets/css/cattle-form.css'
class Dashboard extends Component {
    constructor() {
        super();
        this.state = {
            open: false,
        };
    }
    handleOpenNavBar = () => {
        this.setState({ open: !this.state.open })
    }
    render() {
        return (
            <div id="wrapper" className="wrapper">
                <div id="st-container" className={this.state.open ? "st-container st-effect-1 st-menu-open" : "wrapper dashboard-wrapper st-container"}>
                    <Header
                        header="Live Stock Brand Inspection"
                        openNavBar={this.handleOpenNavBar}
                        open={this.state.open} />
                    <div className="main-middle-area">
                        <section className="dashboard-brand-section">
                            <div className="dashboard-brand-inner">
                                <div className="container">
                                    <DashboardContainer />
                                </div>
                            </div>
                        </section>
                    </div>
                </div>
            </div>

        )
    }
}
export default Dashboard;